
import {defineComponent} from "vue";

export default defineComponent( {
  name: "TabGroupScaffold",
  props: ['title', 'color', 'collapsed', 'groupId', 'countFrames'],
  data() {
    return {
      editMode: false,
      newTitle: null,
    }
  },
  methods: {
    collapse(collapsed: boolean){
      // @ts-ignore
      this.port.postMessage({kind: "collapse-tab-groups", group: this.groupId, collapse: collapsed});
    },
    changeColor(){
      let newColor = this.color
      if (this.color === 'grey'){
        newColor = 'blue'
      }
      if (this.color === 'blue'){
        newColor = 'red'
      }
      if (this.color === 'red'){
        newColor = 'yellow'
      }
      if (this.color === 'yellow'){
        newColor = 'green'
      }
      if (this.color === 'green'){
        newColor = 'pink'
      }
      if (this.color === 'pink'){
        newColor = 'purple'
      }
      if (this.color === 'purple'){
        newColor = 'cyan'
      }
      if (this.color === 'cyan'){
        newColor = 'orange'
      }
      if (this.color === 'orange'){
        newColor = 'grey'
      }

      // @ts-ignore
      this.port.postMessage({kind: "color-tab-groups", group: this.groupId, color: newColor});
    },
    goToViewMode(){
      if (this.newTitle){
        // @ts-ignore
        this.port.postMessage({kind: "title-tab-groups", group: this.groupId, title: this.newTitle});
      }
      this.editMode = false
    },
    goToEditMode(){
      this.editMode = true
      this.newTitle = this.title

      this.$nextTick(() => {
        const html = this.$refs.input as HTMLInputElement
        html.focus()
      });

    }
  }
})

